* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* form styling login */
.form {
  display: flex;
  margin: 20px auto;
  padding: 25px 5px;
  border-radius: 10px;
  align-items: center;
}

.student_first .student__link {
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  font-size: 1.1rem;
}
.student_first .student__link:hover {
  color: #dc3545;
}
.student_second h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.student_second label {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 1.1rem;
}
.student_second .input {
  border-bottom: 2px solid #ccc;
}
.student_second .input {
  margin: 10px 0;
}
.student_second .input input {
  border: none;
  width: 90%;
  padding: 5px 10px;
  background-color: transparent;
  color: white;
}
.student_second .input input:focus {
  border: 0;
  outline: none;
}
.student_second .submit {
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  background-color: rgb(109, 109, 167);
  padding: 10px 15px;
  color: white;
}
