.footer__second {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("https://image.freepik.com/free-photo/low-angle-graduated-students_23-2148522181.jpg");
  padding: 50px 0 10px;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: top;
}
.address {
  margin-top: 80px;
}
.footer__first {
  margin-bottom: -50px;
}
.footer__container {
  padding-top: 50px;
  font-family: "Poppins", sans-serif;
}
.footer_map iframe {
  width: 100%;
  height: 100%;
}
.footer__owner {
  border-top: 1px solid rgba(233, 233, 233, 0.3);
  padding: 15px 0 0;
  font-family: "Poppins", sans-serif;
  justify-content: center;
  margin: 50px auto 0px !important;
  display: flex;
  flex-wrap: wrap;
}
.footer__links a {
  margin-left: 20px;
  color: wheat;
  text-decoration: none;
}
.footer__links a:hover {
  color: white;
}
