.messageBox {
  background-color: blue;
  border-radius: 15px 15px 0px;
  padding: 5px 20px;
  display: inline-block;
  max-width: 80%;
  font-family: "Poppins", sans-serif;
}
.backgroundBlue {
  background-color: blue !important;
  color: black;
}
.colorDark {
  color: white;
}
.backgroundBlue {
  background-color: #f3f3f3 !important;
}
.colorWhite {
  color: white;
}
.sendText {
  color: black;
  font-size: 1.1rem;
  margin: 0 15px;
  font-family: "Poppins", sans-serif;
}
.messageOwner {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1rem;
  word-wrap: break-word;
  font-family: "Poppins", sans-serif;
}

.messageText img {
  vertical-align: middle;
}
.messageContainer {
  display: flex;
  align-items: center;
  padding: 0 2%;
  margin-top: 3px;
}
