.comment__box {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
  align-items: center;
}
.comment__box .comment__area {
  width: 90%;
  padding: 0px 5px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid gray;
  font-size: 18px;
  font-family: "Poppins", sans-serif;
  outline: none;
}
.comment__box button {
  font-family: "Poppins", sans-serif;
  background-color: transparent;
  outline: none;
  border: none;
  font-size: 22px;
}
