.post__feed {
  background-color: white;
  color: black;
  padding: 15px;
}
.post__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid white;
  /* background-color: white; */
}
.post__header .header__left {
  display: flex;
  align-items: center !important;
  padding: 10px 0;
}
.post__header .header__left img {
  width: 50px;
  border-radius: 50%;
  height: 50px;
}
.post__header .header__left p {
  font-size: 18px;
  margin-left: 15px;
  font-family: "Poppins", sans-serif;
}
.header__right button {
  border: none;
  outline: none;
  background-color: transparent;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
}
.post__data .post__image {
  width: 100%;
  margin: 12px 0px;
  object-fit: cover;
}
.post__data span {
  /* color: #dc3545; */
  font-weight: 600;
  text-transform: uppercase;
}
.post__data p {
  margin-top: 15px;
  font-size: 18px;
  font-family: "Poppins", sans-serif;
}
.post__comments {
  display: flex;
  align-items: center;
}
.post__comments span {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}
.post__comments p {
  margin-left: 15px;
}
.post__feed {
  margin: 15px 0 !important;
}
