.outerMessage {
  display: flex;
  justify-content: center;
  margin-top: -50px;
  align-items: center;
  height: 100vh;
}

.innerMessage {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: white;
  border-radius: 0px;
  height: 60%;
  width: 100%;
  padding: 0 !important;
  border: 1px solid rgb(54, 133, 8);
  background-color: white;
}

@media screen and (min-device-width: 350px) and (max-device-width: 600px) {
  .innerMessage {
    margin: 15px;
  }
}
