.infoBar {
  display: flex;
  justify-content: space-between;
  padding: 10px 5px;
  background-color: white;
  border: 2px solid black;
  align-items: center;
}
.leftInnerContainer {
  color: black;
  display: flex;
  align-items: center;
}
.leftInnerContainer h3 {
  color: black;
  margin-left: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 32px;
}
.rightInnerContainer a {
  text-decoration: none;
  color: black;
  font-family: "Roboto", sans-serif;
  font-size: 22px;
  font-weight: 600;
}
