.joinChatOuter {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.joinChatOuter .joinChatInner h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
}
.joinChatOuter h1 {
  border-bottom: 2px solid black;
}
.joinChatOuter input {
  padding: 10px 15px;
  width: 100%;
  font-family: "Poppins", sans-serif;
  margin: 10px 0px;
  border: 1px solid rgba(15, 15, 15, 0.5);
}
.joinChatOuter input:focus {
  outline: none;
}
.joinChatInner button {
  width: 100%;
  padding: 10px 0;
  background-color: rgba(15, 15, 15, 0.5);
  color: whitesmoke;
  font-family: "Poppins", sans-serif;
  border: 1px solid whitesmoke;
}
