.book__post {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.sort_filter {
  flex: 0.1;
}
.createPost {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
  flex: 0.7;
}
.post__list {
  display: flex;
  flex-direction: column;
  /* padding: 16px 24px; */
  padding: 0 !important;
  max-width: 750px;
  /* background-color: #f3f3f3; */
}
.create_post {
  width: 100%;
  margin-top: 10px;
  border: none;
  background-color: transparent;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  outline: none;
}
.price {
  width: 100%;
  margin-top: 0px;
  border: none;
  background-color: transparent;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  outline: none;
}
.create__input {
  background-color: white;
  width: 100%;
  max-width: 750px;
  border-radius: 5px;
  color: black;
  padding: 20px;
}
.create__upload {
  display: flex;
  justify-content: space-between;
}
.icon__upload {
  font-size: 25px;
  cursor: pointer;
}
.create__upload > input {
  display: none;
}
.create__input > p {
  font-family: "Poppins", sans-serif;
  font-size: 1.1rem;
}
.create__btn {
  border: none;
  background-color: transparent;
  font-family: "Poppins", sans-serif;
}
.create__btn::after {
  content: ">";
}
#create_image {
  display: none;
  margin: 10px 0;
  max-width: 140px;
  border-radius: 5px;
}
@media screen and (min-device-width: 350px) and (max-device-width: 600px) {
  .createPost {
    padding: 20px 24px;
  }
}
