.input_form {
  display: flex;
  width: 100% !important;
  height: 60px;

  border-top: 2px solid #d3d3d3;
}
.input_form .input {
  border: none;
  border-radius: 0 !important;
  padding: 2%;
  width: 80%;
  font-size: 1.2em;
}
.input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.sendButton {
  color: white !important;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  background-color: black;
  padding: 15px;
  display: inline-block;
  border: none;
  width: 20%;
}
