.slider__carousel {
  margin: 0 !important;
  width: 100%;
  /* max-height: 850px; */
  height: 850px;
}
.slider__carousel img {
  width: 100% !important;
  height: 950px;
  object-fit: cover;
  /* height: 800px; */
}

@media screen and (min-device-width: 350px) and (max-device-width: 600px) {
  .slider__carousel {
    margin: 0 !important;
    max-height: 350px !important;
  }
  .slider__carousel img {
    height: 350px !important;
  }
}
