.body__about {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
}
.body__about p {
  font-size: 1.1rem;
  font-family: "Poppins", sans-serif;
}
.body__department h2 {
  font-family: "Poppins", sans-serif;
}
.body__department p::before {
  content: "\00B7";
}
.chat__bot {
  position: fixed;
  bottom: 5%;
  right: 20px;
  color: white;
  z-index: 9999999 !important;
}
.chat__bot {
  font-size: 50px !important;
  cursor: pointer;
}
.chat_box {
  position: fixed;
  bottom: 8%;
  right: 70px;
  z-index: 9999999 !important;
}
.program {
  margin-top: -80px;
  margin-bottom: 20px;
  text-align: center;
  z-index: 99999;
}
.program .program__text {
  padding-top: 20px;
  margin-top: 15px;
  background-color: #08a6ac;
  color: white;
  height: 250px;
  font-family: "Poppins", sans-serif;
}
.program a {
  text-decoration: none;
}
.program_icon {
  background-color: white;
  color: black;
  padding: 10px;
  border-radius: 50%;
  font-size: 5rem !important;
  transition: 2s ease-in-out;
}
.program:hover .program__text {
  background-color: white;
  color: black;
  transition: 1s ease-in-out;
}
.program:hover .program_icon {
  background-color: white;
  transition: 1s ease-in-out;
  color: black;
}
.program_icon:hover {
  transform: scale(0.9);
  transition: 1s ease-in-out;
}
.service {
  width: 100%;

  font-family: "Poppins", sans-serif;
  background-image: linear-gradient(rgba(14, 7, 1), rgba(14, 7, 1, 0.5)),
    url("https://image.freepik.com/free-photo/group-diverse-grads-throwing-caps-up-sky_53876-56031.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: top;
  /* height: 450px; */
  padding: 20px 50px;
  background-size: cover;
}
.service__list {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.service .service__box {
  border: 1px solid lightgray;
  margin: 15px;
}
.service .service__box:hover {
  border: 1px solid #08a6ac;
  transition: 0.5s ease-in-out;
}
.service .service__box:hover h1 {
  color: #08a6ac;
  transition: 0.5s ease-in-out;
}
.service .service__box:hover h2 {
  color: #08a6ac;
  transition: 0.5s ease-in-out;
}
.service .service__box {
  text-align: center;
}
.service .service__box h1 {
  font-size: 4.5rem;
  font-weight: 600;
}
.service .service__box h2 {
  font-size: 1.3rem;
  font-weight: 600;
}
.service > h1 {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
